::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--muted-color);
  opacity: 0.5; /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
* {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

input:disabled {
  background-color: var(--disabled-color);
  color: var(--disabled-dark-color);
}

.padding-default {
  padding: 16px;
}

.transaction-destak-item {
  margin-top: 8px;
  padding: 15px 0 0 0;
  background-color: #fff;
}

.error-container {
  text-align: center;
  margin: 0 16px;
  background-color: var(--card-color);
  height: auto;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 8px;
}

.warning-container {
  text-align: center;
  background-color: var(--blue-9);
  padding: 10px 15px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.tabs__list {
  display: flex;
  justify-content: space-between;
}

.tabs__tab {
  padding: 8px;
  width: 100%;
  text-align: center;
  margin: 0 8px;
  background-color: #ffffff;
}

.tabs__content {
  /* position: relative;
  left: 0;
  right: 0;
  height: 100vh;
  overflow: scroll;
