  padding: 16px 0;
}

.selected {
  border: none !important;
  border-bottom: 2px solid var(--text-color) !important;
  font-weight: bold;
  color: var(--text-color);
}

button {
  outline: none;
}
input {
  color: var(--black-color);
}
html {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  color: var(--text-color);
  background-color: #fff;
}

main,
section {
  background-color: #fff;
}

a {
  text-decoration: none;
  color: var(--text-color);
}

p {
  font-size: 12px;
  line-height: 18px;
  margin: 0;
  padding: 0;
}

.h1 {
  font-size: 16px;
  line-height: 24px;
}

.h2 {
  font-size: 16px;
  line-height: 22px;
}

.tab {
  padding: 16px;
}
/* Loading */
.loading {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.error-span {
  padding: 0 16px;
}

.hide {
  opacity: 0;
  visibility: collapse;
  z-index: -1;
}

.loading img {
  transform: rotate(0deg);
  animation-name: loadSpinner;
  animation-timing-function: linear;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
}

.copi-cola-btn {
  display: flex;
  height: 20px;
  align-items: center;
  margin: 24px 0;
  position: relative;
  width: 100%;
  justify-content: center;
}

.tost-copied {
  background-color: #2ba620;
  color: #fff;
  font-size: 14px;
  border-radius: 8px;
  position: absolute;
  bottom: -35px;
  visibility: collapse;
  opacity: 0;
  width: 163px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 500ms ease-in;
  top: 0;
}

.anime-toast {
  visibility: visible;
  opacity: 1;
  transition: all 500ms ease-in;
}

@keyframes loadSpinner {
  to {
    transform: rotate(0deg);
  }
  from {
    transform: rotate(359deg);
  }
}

.form-text {
  font-size: 20px;
  line-height: 26px;
}

.link {
  text-decoration: none;
  font-size: 12px;
  cursor: pointer;
  transition: 0.2s all;
}

.link:hover {
  color: var(--primary-dark-color);
}

.link:active {
  color: var(--primary-dark-color);
}

header {
  display: grid;
  background-color: #fff;
  grid-template-columns: auto 1fr 32px;
  align-items: center;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}

header h1 {
  font-size: 14px;
  line-height: 20px;
  grid-column: 1 / 3;
  justify-self: center;
}

header img.close {
  align-content: flex-end;
  grid-column: 3 / 3;
}

