/* Listas */
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

.list-card-item {
  display: grid;
  grid-template-columns: 1fr 24px;
  grid-template-rows: repeat(2, auto);
  align-items: center;
  padding: 16px;
  margin-bottom: 16px;
  border: 1px solid var(--border-color);
  cursor: pointer;
  border-radius: 8px;
}

.list-card-item p {
  grid-column: 1 / 1;
  grid-row: 1 / 1;
  padding-bottom: 8px;
}

.list-card-item span {
  grid-column: 1 / 1;
  grid-row: 2 / 2;
  width: 256px;
}

.list-card-item .img {
  grid-column: 2 / 2;
  grid-row: 1 / span 2;
}

.input {
  outline: 0;
  border: none;
  font-family: "Bradesco Sans";
  padding: 10px;
  font-weight: 300;
}

.d-block {
  display: block;
}

.message {
  display: grid;
  grid-template-columns: 30px 1fr;
  background-color: #fff;
  border-color: 1px solid var(--border-color);
  border-radius: 8px;
  align-items: center;
  padding: 14px;
}

.alert {
  background: #ffe8ed;
  border-radius: 8px;
  text-align: center;
  padding: 14px;
  color: var(--primary-dark-color);
}

.plus-values {
  display: flex;
  justify-content: space-between;
}

.toggle-container {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.key-options {
  display: grid;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 20%);
  grid-template-columns: 56px 1fr 24px;
  grid-template-rows: repeat(auto, auto);
  align-items: center;
  padding: 0 16px;
  margin: 8px 0;
  height: 74px;
}

.key-options .list-icon {
  grid-column: 1 / 1;
  grid-row: 2 / 3;
}

.list-icon.span {
  grid-row: 1 / span 3;
}

.key-options p {
  grid-column: 2 / 2;
  grid-row: 1 / span 2;
  padding-bottom: 10px;
}

p.randon-key {
  grid-column: 2 / 2;
  grid-row: 1 / span 3;
}

.key-options span {
  grid-column: 2 / 2;
  grid-row: 2 / span 1;
  padding-top: 30px;
}

.key-options .checkbox {
  grid-column: 3 / 3;
  grid-row: 1 / span 3;
}

.selected-default {
  border: 1px solid var(--primary-color);
}

.input-search-container {
  display: flex;
